import React, { useEffect, useState } from 'react';

import './UI.css';

// IMPORT ICONS
import searchIco from '../../../../../assets/icons/search.svg';
import toolsIco from '../../../../../assets/icons/tool.svg';
import editIco from '../../../../../assets/icons/edit-3.svg';
import deleteIco from '../../../../../assets/icons/delete.svg';

const UI = ( props ) => {

    // STUDENT LIST STATE
    const [ Students, setStudents ] = useState([]);
    // MESSAGE SHOW WHILE STUDENT LIST HAS BEEN LOADED STATE
    const [ Message, setMessage ] = useState([]);

    useEffect(
        () => {

            setStudents( props.Students );
            setMessage( props.Message );

        }, [ props.Students, props.Message ]
    )

    return (
        <div className="StudentList">
            
            {/* UPPER PANEL FOR SEARCH AND OTHER CONTROLS */}
            <div className="upper">
                <h3>Student List</h3>
                <div className="Search">
                    <div className="searchDiv">
                        {/* SEARCH ICON */}
                        <img src={ searchIco } className="mr-2" width="20" height="20" alt="Search Icon" />
                        <input 
                            type="text" 
                            className="searchBar" 
                            placeholder="Search Students"
                            onChange={ props.onSearch }
                         />
                        {/* TOOLS ICON */}
                        {/* <img src={ toolsIco } title="Open Search Tools" className="mr-2" width="20" height="20" alt="Tools Icon" /> */}
                    </div>
                </div>
            </div>

            {/* STUDENT LIST */}
            <div className="StdList">
                <table className="table">
                    <thead>
                        <tr>
                            <th>
                                Registration
                            </th>
                            <th>
                                Student Name
                            </th>
                            <th>
                                Gender
                            </th>
                            <th>
                                Residential Telephone
                            </th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            Message.length > 0
                            ?
                            <tr>
                                <td 
                                    // colspan="3"
                                    style={
                                        {
                                            position: 'absolute',
                                            width: '100%',
                                            textAlign: 'center'
                                        }
                                    }
                                > { Message } </td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                            </tr>
                            :
                            Students.map(
                                ( val, index ) => {

                                    return (
                                        <tr key={ index }>
                                            <td> { val.student_id } </td>
                                            <td> { val.student_name } </td>
                                            <td> { val.gender } </td>
                                            <td> { val.residence_telephone } </td>
                                            <td>
                                                {/* EDIT */}
                                                <img src={ editIco } title="Edit Student" className="mr-2" width="20" height="20" alt="Edit Icon" />
                                                {/* DELETE */}
                                                <img src={ deleteIco } title="Delete Student" className="ml-2" width="20" height="20" alt="Delete Icon" />
                                            </td>
                                        </tr>
                                    )

                                }
                            )
                        }
                    </tbody>
                </table>
            </div>

        </div>
    )
}

export default UI;